.main-poster {
  height: 161px;
  margin-top: 13px;
  margin-bottom: 5px;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;

  object-fit: cover; //картинки теперь не растянуты

  @media screen and (min-width: 767.99px) {
    width: 180px;
    height: 227px;
  }

  // background-color: black;
}

.main-name,
.main-date,
.main-location {
  text-align: center;
  font-size: 14px;
  line-height: 1.2;
  // display: flex;
  // justify-content: center;
  // align-items: center;
  // text-align: center;
  // margin-top: 5px;
  // font-size: 16px;
