  }
}

.main-date {
  font-size: normal;
  font-size: 14px;
  margin-bottom: 5px;
}

.main-name {
  font-weight: 700;
  color: var(--pink-color);
  margin-bottom: 5px;
}

.main-location {
  font-size: 12px;
  font-weight: 600;
  // cursor: pointer;

  @media screen and (min-width: 767.99px) {
    font-size: 14px;
  }
}

// Error geolocator

.error_Geo {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.error_Geo h2 {
